.is-hidden {
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
}

.form-wrapper {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto 0;
  padding: 10px 20px;
  transition: background-color .15s linear;
  display: block;
}

.input-field {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 16px;
}

.gallery {
  max-width: 100%;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin: 0 auto;
  display: grid;
}

.photo-card {
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
}

.photo-card img {
  max-width: 100%;
  height: 290px;
  margin: 0 auto;
  display: block;
}

.info {
  height: 50px;
  gap: 20px;
  margin-top: 10px;
  display: flex;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto 0;
  padding: 10px 20px;
  transition: background-color .15s linear;
  display: block;
}

.load-more:hover, .load-more:focus, .search:hover, .search:focus {
  background-color: #00aeff;
}

.load-more:active, .load-more:active, .search:active, .search:active {
  background-color: #0003ff;
}

